import React, { useState, useEffect } from 'react';
import ReactPaginate from 'react-paginate';
import LoaderIcon from './loader';
import categories from './category';
import './garment-card.scss';
import './search-bar.scss';

// create a React functional component
const GarmentCard = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [garments, setGarment] = useState([]);
  const [category, setCategory] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [pageCount, setpageCount] = useState(0);

  let limit = 16;
  useEffect(() => {
    const getGarments = async () => {
      const res = await fetch(
        category
          ? `/api/v1/garments/${category}?page=1&limit=${limit}`
          : `/api/v1/garments?page=1&limit=${limit}`
      );
      const data = await res.json();
      setpageCount(Math.ceil(data.total / limit));
      if (data.err === true) {
        setGarment(data.garments);
        setError(true);
      } else {
        setLoading(true);
        setGarment(data.garments);
      }
    };
    getGarments();
  }, [limit, category]);
  const getGarments = async (currentPage) => {
    const res = await fetch(
      category
        ? `/api/v1/garments/${category}?page=${currentPage}&limit=${limit}`
        : `/api/v1/garments?page=${currentPage}&limit=${limit}`
    );
    const data = await res.json();
    setGarment(data.garments);
    return data.garments;
  };
  // function to handle pagination
  const handlePageClick = async (data) => {
    let currentPage = data.selected + 1;
    const commentsFormServer = await getGarments(currentPage);
    setGarment(commentsFormServer);
    // scroll to the top
    window.scrollTo(0, 0);
  };
  //handle category
  const handleCategory = (e) => {
    return setCategory(e.target.value);
  };
  return (
    <div className='container'>
      <h3 className='brand-logo'>
        InteliStyle
        <p className='subtitle'>Lead tech-co in Fastion Industery</p>
        <p className='banner'>
          See all the Collections and a like-recomendation for your outfit and
          body shape
        </p>
      </h3>
      <select className='dropdown' onChange={handleCategory}>
        <option value=''>Select Category</option>
        {categories.map((category) => (
          <option
            key={category.value}
            className='dropdown-item'
            value={category.value}
          >
            {category.name}
          </option>
        ))}
      </select>
      <div className='wrapper'>
        <img
          className='search-icon'
          src='data:image/svg+xml;utf8;base64,'
          alt={'search-icon'}
        />
        <input
          className='search'
          type={'text'}
          placeholder={'Search'}
          onChange={(event) => {
            setSearchTerm(event.target.value);
          }}
        />
      </div>
      <div className='preview'>
        {loading ? (
          garments
            .filter((val) => {
              if (searchTerm === '') {
                return val;
              } else if (
                val.product_title
                  .toLowerCase()
                  .includes(searchTerm.toLowerCase())
              ) {
                return val;
              }
            })
            .map((garment) => (
              <div key={garment._id} className='collection-item'>
                <div
                  className='image'
                  style={{
                    backgroundImage: `url(${garment.image_urls[0]})`,
                  }}
                />
                <div className='collection-footer'>
                  <span className='name'>{garment.product_title}</span>
                  <span className='price'>$ {garment.price}</span>
                </div>
              </div>
            ))
        ) : (
          <>
            <LoaderIcon />
            {error && <p>We are working on your request ✨</p>}
          </>
        )}
      </div>
      <ReactPaginate
        previousLabel={'previous'}
        nextLabel={'next'}
        breakLabel={'...'}
        pageCount={pageCount}
        marginPagesDisplayed={2}
        pageRangeDisplayed={3}
        onPageChange={handlePageClick}
        containerClassName={'pagination justify-content-center'}
        pageClassName={'page-item'}
        pageLinkClassName={'page-link'}
        previousClassName={'page-item'}
        previousLinkClassName={'page-link'}
        nextClassName={'page-item'}
        nextLinkClassName={'page-link'}
        breakClassName={'page-item'}
        breakLinkClassName={'page-link'}
        activeClassName={'active'}
      />
    </div>
  );
};
export default GarmentCard;
